@import url(https://code.ionicframework.com/ionicons/2.0.1/css/ionicons.min.css);

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

.container {
    max-width: 900px;
    display: flex;
    justify-content: space-evenly;
    margin: 0 auto;
}

.card-wrapper {
    width: 400px;
    height: 500px;
    position: relative;
}

.card {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 350px;
    height: 450px;
    transform: translate(-50%, -50%);
    border-radius: 16px;
    overflow: hidden;
    box-shadow: 0 5px 18px rgba(0, 0, 0, 0.6);
    cursor: pointer;
    transition: 0.5s;

    .card-image {
        position: absolute;
        top: 0px;
        left: 0px;
        width: 100%;
        height: 100%;
        z-index: 2;
        background-color: #000;
        transition: .5s;
        text-align: center;
    }
    img{
        scale: 2.2;
        width: 550px;
    }

    &:hover img {
        opacity: 0.4;
        transition: .5s;
    }
}

.card:hover .card-image {
    transform: translateY(-100px);
    transition: all .9s;
}

/**** Social Icons *****/

.social-icons {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 3;
    display: flex;

    li {
        list-style: none;

        a {
            position: relative;
            display: block;
            width: 50px;
            height: 50px;
            line-height: 50px;
            text-align: center;
            font-size: 23px;
            color: #333;
            font-weight: bold;
            margin: 0 6px;
            transition: .4s;
            transform: translateY(200px);
            opacity: 0;

            i {
                padding: 10px 5px;
                display: inline-block;
                font-size: 32px;
                color: #ffffff;
                text-align: center;
                opacity: 0.65;
            }
            i:hover {
                opacity: 1;
                -webkit-transition: all 0.35s ease;
                transition: all 0.35s ease;
              }

        }
    }
}

.card:hover .social-icons li a {
    transform: translateY(0px);
    opacity: 1;
}

.social-icons li a:hover {

    transition: .2s;

    .fab {
        color: #fff;
    }
}

.social-icons li a .fab {
    transition: .8s;

    &:hover {
        transform: rotateY(360deg);
        color: #fff;
    }
}

.card:hover li:nth-child(1) a {
    transition-delay: 0.1s;
}

.card:hover li:nth-child(2) a {
    transition-delay: 0.2s;
}

.card:hover li:nth-child(3) a {
    transition-delay: 0.3s;
}

.card:hover li:nth-child(4) a {
    transition-delay: 0.4s;
}

/**** Personal Details ****/

.details {
    position: absolute;
    bottom: 0;
    left: 0;
    background: #fff;
    width: 100%;
    height: 120px;
    z-index: 1;
    padding: 10px;

    h2 {
        margin: 30px 0;
        padding: 0;
        text-align: center;

        .job-title {
            font-size: 1rem;
            line-height: 2.5rem;
            color: #181818;
            font-weight: 300;
        }
    }
}

.jane {
    position: absolute;
    bottom: -120px;
    left: 0;
    opacity: 0;
    background: #fff;
    width: 100%;
    height: 120px;
    z-index: 3;
    padding: 10px;
    transition: .4s;
}

.profile-two .social-icons li a {
    border-radius: 50%;
}

.card:hover .profile-img--two {
    transform: rotateY(180deg);
}

.card:hover .jane {
    bottom: 0;
    left: 0;
    transition-delay: 0.5s;
    opacity: 1;
}